-only {\r\n    background: none;\r\n    border: none;\r\n    color: $off-white;\r\n    padding: 0;\r\n}\r\n\r\n@media (min-width: $desktop-breakpoint) {\r\n    .main-button {\r\n        margin-bottom: $xl-size;\r\n    }\r\n}",".widget-header {\r\n    background: $blue;\r\n    color: $off-white;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: $m-size;\r\n}\r\n\r\n.widget-header__title {\r\n    margin: 0;\r\n}\r\n\r\n.widget {\r\n    background: $light-blue;\r\n    margin-bottom: $xl-size;\r\n}\r\n\r\n.widget__message {\r\n    border-bottom: $xxs-size solid lighten($light-blue, 10%);\r\n    color: $off-white;\r\n    margin: 0;\r\n    padding: $l-size;\r\n    text-align: center;\r\n}",".option {\r\n    border-bottom: $xxs-size solid lighten($light-blue, 10%);\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: $l-size $m-size;\r\n}\r\n\r\n.option__text {\r\n    color: $white;\r\n    font-weight: 500;\r\n    font-size: $mm-size;\r\n    margin: 0;\r\n    word-break: break-all;\r\n}",".add-option-error {\r\n    color: $off-white;\r\n    font-size: $mm-size;\r\n    font-style: italic;\r\n    font-weight: 500;\r\n    margin: $m-size 0 0 0;\r\n    text-align: center;\r\n}\r\n\r\n// .add-option-error {\r\n//     color: $off-white;\r\n//     font-style: italic;\r\n//     margin: $m-size 0 0 0;\r\n//     padding: 0 $m-size;\r\n// }\r\n\r\n.add-option {\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: $m-size;\r\n}\r\n\r\n.add-option__input {\r\n    background: $dark-blue;\r\n    border: none;\r\n    border-bottom: $xs-size solid darken($dark-blue, 10%);\r\n    color: $white;\r\n    flex-grow: 1;\r\n    margin: 0 0 $s-size 0;\r\n    padding: $s-size;\r\n}\r\n\r\n@media (min-width: $desktop-breakpoint) {\r\n    .add-option {\r\n        flex-direction: row;\r\n    }\r\n\r\n    .add-option__input {\r\n        margin: 0 $s-size 0 0;\r\n    }\r\n}",".ReactModalPortal > div {\r\n    opacity: 0;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay {\r\n    align-items: center;\r\n    display: flex;\r\n    justify-content: center;\r\n    transition: opacity 200ms ease-in-out;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--after-open {\r\n    opacity: 1;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--before-close {\r\n    opacity: 0;\r\n}\r\n\r\n.modal {\r\n    background: $light-blue;\r\n    color: $off-white;\r\n    max-width: 30rem;\r\n    outline: none;\r\n    padding: $l-size;\r\n    text-align: center;\r\n}\r\n\r\n.modal__title {\r\n    margin: 0 0 $m-size 0;\r\n}\r\n\r\n.modal__body {\r\n    font-size: $mm-size;\r\n    font-weight: 300;\r\n    margin: 0 0 $l-size 0;\r\n    word-break: break-all;\r\n}"],"sourceRoot":""}]);
